import { WebpackAsyncRoute } from '@angularclass/webpack-toolkit';
import { Routes, RouterModule } from '@angular/router';
import { Home } from './home';
import { NoContent } from './no-content';
import {Objects} from './objects';
import {Notes} from './notes';
import {Cards} from './cards';


import {Indicators} from './data_page/indicators';
import {Acts} from './data_page/acts';

import { DataResolver } from './app.resolver';

// AngularClass
import { provideWebpack } from '@angularclass/webpack-toolkit';
import { providePrefetchIdleCallbacks } from '@angularclass/request-idle-callback';


export const ROUTES: Routes = [
  { path: '',      component: Acts },
  { path: 'home',  component: Acts },
  // make sure you match the component type string to the require in asyncRoutes
  { path: 'about', component: 'About',
    resolve: {
      'yourData': DataResolver
    }},
  { path: 'cards', component: Cards,
    resolve: {
      'yourData': DataResolver
    }},
  { path: 'objects', component: Objects,
    resolve: {
      'yourData': DataResolver
    }},
  { path: 'notes', component: Notes,
    resolve: {
      'yourData': DataResolver
    }},
  { path: 'indicators', component: Indicators,
    resolve: {
      'yourData': DataResolver
    }},
  { path: 'acts', component: Acts,
    resolve: {
      'yourData': DataResolver
    }},


  // async components with children routes must use WebpackAsyncRoute

  { path: '**',    component: NoContent },
];


// Async load a component using Webpack's require with es6-promise-loader and webpack `require`
// asyncRoutes is needed for our @angularclass/webpack-toolkit that will allow us to resolve
// the component correctly

export const asyncRoutes: AsyncRoutes = {
  // we have to use the alternative syntax for es6-promise-loader to grab the routes
  'Cards': require('es6-promise-loader!./cards'),
  'About': require('es6-promise-loader!./about'),

'Objects': require('es6-promise-loader!./objects'),
'Notes': require('es6-promise-loader!./notes'),

'Indicators': require('es6-promise-loader!./data_page/indicators'),
'Acts': require('es6-promise-loader!./data_page/acts'),


};


// Optimizations for initial loads
// An array of callbacks to be invoked after bootstrap to prefetch async routes
export const prefetchRouteCallbacks: Array<IdleCallbacks> = [
  asyncRoutes['About'],
  asyncRoutes['Cards'],
  asyncRoutes['Objects'],
  asyncRoutes['Notes'],
  asyncRoutes['Indicators'],
  asyncRoutes['Acts'],
   // es6-promise-loader returns a function
];


// Es6PromiseLoader and AsyncRoutes interfaces are defined in custom-typings


export const ROUTING_PROVIDERS = [
  provideWebpack(asyncRoutes),
  providePrefetchIdleCallbacks(prefetchRouteCallbacks)
];



